/* Reset and base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  position: relative;
  direction: rtl;
  text-align: right;
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background:
    radial-gradient(circle at 20% 80%, rgba(120, 119, 198, 0.3) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 119, 198, 0.3) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(120, 219, 255, 0.2) 0%, transparent 50%);
  pointer-events: none;
  z-index: -1;
}

.App {
  min-height: 100vh;
}

/* Dashboard Container */
.dashboard-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 2rem;
  position: relative;
}

/* Dashboard Header */
.dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 3rem;
  gap: 2rem;
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 2.5rem;
  border: 1px solid rgba(255, 255, 255, 0.35);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden;
}

.dashboard-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #667eea, #764ba2, #667eea);
  background-size: 200% 100%;
  animation: shimmer 3s ease-in-out infinite;
}

@keyframes shimmer {
  0% {
    background-position: 200% 0;
  }

  100% {
    background-position: -200% 0;
  }
}

.dashboard-header>div:first-child {
  flex: 1;
}

.dashboard-header>button {
  flex-shrink: 0;
  align-self: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 1rem 2rem;
  border-radius: 16px;
  font-size: 1.1rem;
  font-weight: 700;
  border: none;
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.dashboard-header>button::before {
  content: '';
  position: absolute;
  top: 0;
  right: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: right 0.6s;
}

.dashboard-header>button:hover::before {
  right: 100%;
}

.dashboard-header>button:hover {
  background: linear-gradient(135deg, #5a6fd8 0%, #6a4b93 100%);
  transform: translateY(-4px);
  box-shadow: 0 12px 35px rgba(102, 126, 234, 0.5);
  border-color: rgba(255, 255, 255, 0.3);
}

.dashboard-header>button:active {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
}

.dashboard-header h1 {
  color: #ffffff;
  font-size: 3rem;
  margin-bottom: 3rem;
  text-shadow: 0 6px 20px rgba(0, 0, 0, 0.8), 0 3px 10px rgba(0, 0, 0, 0.6);
  font-weight: 900;
  line-height: 1.1;
  letter-spacing: -1px;
  filter: drop-shadow(0 3px 10px rgba(0, 0, 0, 0.5));
  text-align: center;
  position: relative;
  z-index: 2;
}

.dashboard-header h1::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 120%;
  height: 120%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
  z-index: -1;
  animation: glow 3s ease-in-out infinite alternate;
}

@keyframes glow {
  0% {
    opacity: 0.3;
    transform: translate(-50%, -50%) scale(1);
  }

  100% {
    opacity: 0.7;
    transform: translate(-50%, -50%) scale(1.1);
  }
}

.dashboard-header p {
  color: #ffffff;
  font-size: 1.5rem;
  margin-bottom: 1rem;
  line-height: 1.6;
  font-weight: 500;
  text-shadow: 0 3px 12px rgba(0, 0, 0, 0.8), 0 2px 6px rgba(0, 0, 0, 0.6);
  filter: drop-shadow(0 2px 6px rgba(0, 0, 0, 0.5));
  text-align: center;
}

.admin-notice {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2), 0 1px 2px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  min-width: 250px;
  justify-content: center;
  font-weight: 700;
  font-size: 0.8rem;
  color: #333;
}

.admin-notice::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 2px;
  background: linear-gradient(90deg, #667eea, #764ba2);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.admin-notice:hover::before {
  transform: scaleX(1);
}

.admin-notice:hover {
  background: rgba(255, 255, 255, 1);
  transform: translateY(-1px);
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25), 0 1px 3px rgba(0, 0, 0, 0.15);
}

/* Admin Status Section */
.admin-status-section {
  margin-top: 1.5rem;
  padding: 1.5rem;
  background: rgba(255, 255, 255, 0.15);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.password-status {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #48bb78;
  font-weight: 600;
  margin-bottom: 1rem;
}

.password-status .icon-small {
  width: 18px;
  height: 18px;
}

.verified-callers-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 1rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.15);
  gap: 1rem;
}

.callers-label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #2d3748;
  font-weight: 500;
  flex: 1;
}

.callers-label .icon-small {
  width: 18px;
  height: 18px;
}

.verified-callers-info .btn-small {
  padding: 0.5rem 1rem;
  font-size: 0.875rem;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: #2d3748;
  transition: all 0.2s ease;
  cursor: pointer;
  font-weight: 500;
  min-height: 36px;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  flex-shrink: 0;
  white-space: nowrap;
}

.verified-callers-info .btn-small:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border-color: rgba(255, 255, 255, 0.4);
}

.verified-callers-info .btn-small:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.2);
  color: rgba(45, 55, 72, 0.5);
  pointer-events: none;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-user-drag: none;
  -webkit-touch-callout: none;
}

.verified-callers-info .btn-small:active {
  transform: translateY(0);
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
}

.verified-callers-info .btn-small:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.2);
}

/* Twilio Balance */
.twilio-balance {
  margin-top: 0.5rem;
}

.balance-card {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  border: 2px solid rgba(255, 255, 255, 0.8);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2), 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  min-width: 250px;
  justify-content: flex-start;
}

.balance-card::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 2px;
  background: linear-gradient(90deg, #667eea, #764ba2);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.balance-card:hover::before {
  transform: scaleX(1);
}

.balance-card:hover {
  background: rgba(255, 255, 255, 1);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.25), 0 4px 12px rgba(0, 0, 0, 0.15);
}

.balance-icon {
  font-size: 1rem;
  color: #667eea;
  filter: drop-shadow(0 1px 2px rgba(102, 126, 234, 0.4));
  flex-shrink: 0;
  animation: pulse 2s infinite;
  background: linear-gradient(135deg, #667eea, #764ba2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

@keyframes pulse {

  0%,
  100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }
}

.balance-details {
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  flex: 1;
  align-items: center;
  justify-content: flex-start;
}

.balance-label {
  color: #333;
  font-size: 0.7rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.2px;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  white-space: nowrap;
}

.balance-amount {
  color: #667eea;
  font-size: 0.8rem;
  font-weight: 800;
  text-shadow: 0 1px 1px rgba(102, 126, 234, 0.3);
  font-family: 'Courier New', monospace;
  background: linear-gradient(135deg, #667eea, #764ba2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  filter: drop-shadow(0 1px 1px rgba(0, 0, 0, 0.2));
}

/* Buttons */
.btn {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 2rem;
  border: none;
  border-radius: 16px;
  font-size: 1.1rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  text-decoration: none;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  direction: rtl;
  border: 1px solid rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  right: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: right 0.6s;
}

.btn:hover::before {
  right: 100%;
}

.btn-primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
}

.btn-primary:hover {
  background: linear-gradient(135deg, #5a6fd8 0%, #6a4b93 100%);
  transform: translateY(-4px);
  box-shadow: 0 12px 35px rgba(102, 126, 234, 0.5);
  border-color: rgba(255, 255, 255, 0.3);
}

.btn-secondary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
}

.btn-secondary:hover {
  background: linear-gradient(135deg, #5a6fd8 0%, #6a4b93 100%);
  transform: translateY(-4px);
  box-shadow: 0 12px 35px rgba(102, 126, 234, 0.5);
  border-color: rgba(255, 255, 255, 0.3);
}

.btn-danger {
  background: linear-gradient(135deg, #dc3545 0%, #c82333 100%);
  color: white;
  box-shadow: 0 8px 25px rgba(220, 53, 69, 0.4);
}

.btn-danger:hover {
  background: linear-gradient(135deg, #c82333 0%, #a71e2a 100%);
  transform: translateY(-4px);
  box-shadow: 0 12px 35px rgba(220, 53, 69, 0.5);
  border-color: rgba(255, 255, 255, 0.3);
}

.btn-small {
  padding: 0.875rem 1.5rem;
  font-size: 1rem;
  border-radius: 12px;
}

/* Small Secondary Button - Same as regular secondary */
.btn-small.btn-secondary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
}

.btn-small.btn-secondary:hover {
  background: linear-gradient(135deg, #5a6fd8 0%, #6a4b93 100%);
  transform: translateY(-4px);
  box-shadow: 0 12px 35px rgba(102, 126, 234, 0.5);
  border-color: rgba(255, 255, 255, 0.3);
}

.btn-icon {
  width: 20px;
  height: 20px;
  transition: transform 0.3s ease;
  order: 2;
  filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.2));
}

.btn span {
  order: 1;
}

.btn:hover .btn-icon {
  transform: scale(1.1);
}

/* Button Disabled States */
.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none !important;
  box-shadow: none !important;
}

.btn:disabled:hover {
  transform: none !important;
  box-shadow: none !important;
}

.btn:disabled::before {
  display: none;
}

/* Forms */
.form-container {
  background: rgba(255, 255, 255, 0.98);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2.5rem;
  margin-bottom: 2.5rem;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  animation: slideIn 0.5s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.form-container h3 {
  color: #333;
  margin-bottom: 1rem;
  font-size: 1.8rem;
  font-weight: 700;
  background: linear-gradient(135deg, #667eea, #764ba2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.form-container p {
  color: #666;
  margin-bottom: 2rem;
  font-size: 1.1rem;
  line-height: 1.6;
}

/* Form Groups */
.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: #2d3748;
  font-size: 0.95rem;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 0.875rem 1rem;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: white;
  color: #2d3748;
  font-family: inherit;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
  transform: translateY(-1px);
}

.form-group input:disabled,
.form-group select:disabled {
  background-color: #f7fafc;
  color: #a0aec0;
  cursor: not-allowed;
  opacity: 0.7;
  border-color: #e2e8f0;
}

.form-group select:disabled:hover {
  border-color: #e2e8f0;
  background-color: #f7fafc;
}

.form-group select {
  cursor: pointer;
  appearance: none;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%236b7280' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='m6 8 4 4 4-4'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 0.75rem center;
  background-size: 1.5em 1.5em;
  padding-right: 2.5rem;
  text-align: right;
  direction: rtl;
}

.form-group select option {
  padding: 0.5rem;
  background: white;
  color: #2d3748;
  text-align: right;
  direction: rtl;
}

.form-group select option:hover {
  background: #f7fafc;
}

.form-group select:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
  transform: translateY(-1px);
}

.form-group select:focus-visible {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
  transform: translateY(-1px);
}

.form-group small {
  display: block;
  margin-top: 0.5rem;
  color: #718096;
  font-size: 0.875rem;
  line-height: 1.4;
}

.form-actions {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
  justify-content: flex-end;
}

/* Gates Grid */
.gates-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
  gap: 2rem;
  margin-top: 2.5rem;
}

.gate-card {
  background: rgba(255, 255, 255, 0.98);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  position: relative;
  overflow: hidden;
  direction: rtl;
}

.gate-card::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 4px;
  background: linear-gradient(90deg, #667eea, #764ba2);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.gate-card:hover::before {
  transform: scaleX(1);
}

.gate-card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 30px 80px rgba(0, 0, 0, 0.15);
}

.gate-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  direction: rtl;
}

.gate-header h3 {
  color: #333;
  font-size: 1.5rem;
  font-weight: 700;
  background: linear-gradient(135deg, #667eea, #764ba2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.gate-actions-header {
  display: flex;
  gap: 0.75rem;
  direction: rtl;
}

.gate-info {
  margin-bottom: 1.5rem;
  padding: 1rem;
  background: rgba(102, 126, 234, 0.05);
  border-radius: 12px;
  border-right: 4px solid #667eea;
  direction: rtl;
}

.gate-info p {
  color: #666;
  margin-bottom: 0.75rem;
  font-size: 0.95rem;
  line-height: 1.5;
}

.gate-info strong {
  color: #333;
  font-weight: 600;
}

.gate-authorized {
  margin-bottom: 2rem;
  padding: 1rem;
  background: rgba(40, 167, 69, 0.05);
  border-radius: 12px;
  border-right: 4px solid #28a745;
  direction: rtl;
}

.gate-authorized h4 {
  color: #333;
  font-size: 1.1rem;
  margin-bottom: 0.75rem;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-weight: 600;
}

.icon-small {
  width: 18px;
  height: 18px;
  color: #28a745;
  display: inline-block;
  vertical-align: middle;
  margin-left: 0.5rem;
  flex-shrink: 0;
}

.icon-big {
  width: 64px;
  height: 64px;
  color: #28a745;
  display: inline-block;
  vertical-align: middle;
  margin-left: 0.5rem;
}

.authorized-number {
  color: #28a745;
  font-weight: 700;
  font-size: 1rem;
  font-family: 'Courier New', monospace;
  background: rgba(40, 167, 69, 0.1);
  padding: 0.5rem 1rem;
  border-radius: 8px;
  border: 1px solid rgba(40, 167, 69, 0.3);
  display: inline-block;
}

.authorized-number.hidden {
  color: #6c757d;
  font-family: monospace;
  letter-spacing: 3px;
  user-select: none;
  background: rgba(108, 117, 125, 0.1);
  border: 1px solid rgba(108, 117, 125, 0.3);
}

.password-notice {
  color: #6c757d;
  font-style: italic;
  font-size: 0.85rem;
  margin-top: 0.75rem;
  text-align: center;
  padding: 0.5rem;
  background: rgba(108, 117, 125, 0.1);
  border-radius: 8px;
  border: 1px solid rgba(108, 117, 125, 0.2);
}

.gate-actions {
  display: flex;
  gap: 1rem;
}

/* Gate Open Button Special Styling */
.gate-open-btn {
  flex: 1;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  position: relative;
  overflow: hidden;
}

.gate-open-btn::before {
  content: '';
  position: absolute;
  top: 0;
  right: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: right 0.6s;
}

.gate-open-btn:hover::before {
  right: 100%;
}

.gate-open-btn:hover {
  background: linear-gradient(135deg, #5a6fd8 0%, #6a4b93 100%);
  transform: translateY(-4px);
  box-shadow: 0 12px 35px rgba(102, 126, 234, 0.5);
  border-color: rgba(255, 255, 255, 0.3);
}

.gate-open-btn:active {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
}

/* Success Message */
.success-message {
  background: linear-gradient(135deg, #d4edda, #c3e6cb);
  color: #155724;
  padding: 1.5rem;
  border-radius: 12px;
  margin-bottom: 1.5rem;
  border: 1px solid #c3e6cb;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 8px 25px rgba(40, 167, 69, 0.2);
  animation: slideIn 0.5s ease-out;
  direction: rtl;
  text-align: right;
}

.success-message button {
  background: none;
  border: none;
  color: #155724;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.3s ease;
  margin-right: auto;
  margin-left: 0;
}

.success-message button:hover {
  background: rgba(40, 167, 69, 0.2);
  transform: scale(1.1);
}

/* Error Message */
.error-message {
  background: linear-gradient(135deg, #f8d7da, #f5c6cb);
  color: #721c24;
  padding: 1.5rem;
  border-radius: 12px;
  margin-bottom: 1.5rem;
  border: 1px solid #f5c6cb;
  display: flex;

  /* Reset and base styles */
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    min-height: 100vh;
    position: relative;
    direction: rtl;
    text-align: right;
  }

  body::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background:
      radial-gradient(circle at 20% 80%, rgba(120, 119, 198, 0.3) 0%, transparent 50%),
      radial-gradient(circle at 80% 20%, rgba(255, 119, 198, 0.3) 0%, transparent 50%),
      radial-gradient(circle at 40% 40%, rgba(120, 219, 255, 0.2) 0%, transparent 50%);
    pointer-events: none;
    z-index: -1;
  }

  .App {
    min-height: 100vh;
  }

  /* Dashboard Container */
  .dashboard-container {
    max-width: 1400px;
    margin: 0 auto;
    padding: 2rem;
    position: relative;
  }

  /* Dashboard Header */
  .dashboard-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 3rem;
    gap: 2rem;
    background: rgba(255, 255, 255, 0.25);
    backdrop-filter: blur(20px);
    border-radius: 24px;
    padding: 2.5rem;
    border: 1px solid rgba(255, 255, 255, 0.35);
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2);
    position: relative;
    overflow: hidden;
  }

  .dashboard-header::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: linear-gradient(90deg, #667eea, #764ba2, #667eea);
    background-size: 200% 100%;
    animation: shimmer 3s ease-in-out infinite;
  }

  @keyframes shimmer {
    0% {
      background-position: 200% 0;
    }

    100% {
      background-position: -200% 0;
    }
  }

  .dashboard-header>div:first-child {
    flex: 1;
  }

  .dashboard-header>button {
    flex-shrink: 0;
    align-self: center;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    padding: 1rem 2rem;
    border-radius: 16px;
    font-size: 1.1rem;
    font-weight: 700;
    border: none;
    box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    position: relative;
    overflow: hidden;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
  }

  .dashboard-header>button::before {
    content: '';
    position: absolute;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
    transition: right 0.6s;
  }

  .dashboard-header>button:hover::before {
    right: 100%;
  }

  .dashboard-header>button:hover {
    background: linear-gradient(135deg, #5a6fd8 0%, #6a4b93 100%);
    transform: translateY(-4px);
    box-shadow: 0 12px 35px rgba(102, 126, 234, 0.5);
    border-color: rgba(255, 255, 255, 0.3);
  }

  .dashboard-header>button:active {
    transform: translateY(-2px);
    box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
  }

  .dashboard-header h1 {
    color: #ffffff;
    font-size: 3rem;
    margin-bottom: 3rem;
    text-shadow: 0 6px 20px rgba(0, 0, 0, 0.8), 0 3px 10px rgba(0, 0, 0, 0.6);
    font-weight: 900;
    line-height: 1.1;
    letter-spacing: -1px;
    filter: drop-shadow(0 3px 10px rgba(0, 0, 0, 0.5));
    text-align: center;
    position: relative;
    z-index: 2;
  }

  .dashboard-header h1::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 120%;
    height: 120%;
    background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
    z-index: -1;
    animation: glow 3s ease-in-out infinite alternate;
  }

  @keyframes glow {
    0% {
      opacity: 0.3;
      transform: translate(-50%, -50%) scale(1);
    }

    100% {
      opacity: 0.7;
      transform: translate(-50%, -50%) scale(1.1);
    }
  }

  .dashboard-header p {
    color: #ffffff;
    font-size: 1.5rem;
    margin-bottom: 1rem;
    line-height: 1.6;
    font-weight: 500;
    text-shadow: 0 3px 12px rgba(0, 0, 0, 0.8), 0 2px 6px rgba(0, 0, 0, 0.6);
    filter: drop-shadow(0 2px 6px rgba(0, 0, 0, 0.5));
    text-align: center;
  }

  .admin-notice {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.5rem 1rem;
    background: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(10px);
    border-radius: 12px;
    border: 1px solid rgba(255, 255, 255, 0.8);
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2), 0 1px 2px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
    min-width: 250px;
    justify-content: center;
    font-weight: 700;
    font-size: 0.8rem;
    color: #333;
  }

  .admin-notice::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    height: 2px;
    background: linear-gradient(90deg, #667eea, #764ba2);
    transform: scaleX(0);
    transition: transform 0.3s ease;
  }

  .admin-notice:hover::before {
    transform: scaleX(1);
  }

  .admin-notice:hover {
    background: rgba(255, 255, 255, 1);
    transform: translateY(-1px);
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25), 0 1px 3px rgba(0, 0, 0, 0.15);
  }

  /* Admin Status Section */
  .admin-status-section {
    margin-top: 1.5rem;
    padding: 1.5rem;
    background: rgba(255, 255, 255, 0.15);
    border-radius: 16px;
    border: 1px solid rgba(255, 255, 255, 0.2);
  }

  .password-status {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: #48bb78;
    font-weight: 600;
    margin-bottom: 1rem;
  }

  .password-status .icon-small {
    width: 18px;
    height: 18px;
  }

  .verified-callers-info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1rem;
    padding: 1rem;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    border: 1px solid rgba(255, 255, 255, 0.15);
    gap: 1rem;
  }

  .callers-label {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: #2d3748;
    font-weight: 500;
    flex: 1;
  }

  .callers-label .icon-small {
    width: 18px;
    height: 18px;
  }

  .verified-callers-info .btn-small {
    padding: 0.5rem 1rem;
    font-size: 0.875rem;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.3);
    color: #2d3748;
    transition: all 0.2s ease;
    cursor: pointer;
    font-weight: 500;
    min-height: 36px;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    flex-shrink: 0;
    white-space: nowrap;
  }

  .verified-callers-info .btn-small:hover {
    background: rgba(255, 255, 255, 0.3);
    transform: translateY(-1px);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    border-color: rgba(255, 255, 255, 0.4);
  }

  .verified-callers-info .btn-small:disabled {
    opacity: 0.5;
    cursor: not-allowed;
    transform: none;
    box-shadow: none;
    background: rgba(255, 255, 255, 0.1);
    border-color: rgba(255, 255, 255, 0.2);
    color: rgba(45, 55, 72, 0.5);
    pointer-events: none;
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: transparent;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    -webkit-user-drag: none;
    -webkit-touch-callout: none;
  }

  .verified-callers-info .btn-small:active {
    transform: translateY(0);
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
  }

  .verified-callers-info .btn-small:focus {
    outline: none;
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.2);
  }

  /* Twilio Balance */
  .twilio-balance {
    margin-top: 0.5rem;
  }

  .balance-card {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.5rem 1rem;
    background: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(10px);
    border-radius: 12px;
    border: 2px solid rgba(255, 255, 255, 0.8);
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2), 0 2px 8px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
    min-width: 250px;
    justify-content: flex-start;
  }

  .balance-card::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    height: 2px;
    background: linear-gradient(90deg, #667eea, #764ba2);
    transform: scaleX(0);
    transition: transform 0.3s ease;
  }

  .balance-card:hover::before {
    transform: scaleX(1);
  }

  .balance-card:hover {
    background: rgba(255, 255, 255, 1);
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.25), 0 4px 12px rgba(0, 0, 0, 0.15);
  }

  .balance-icon {
    font-size: 1rem;
    color: #667eea;
    filter: drop-shadow(0 1px 2px rgba(102, 126, 234, 0.4));
    flex-shrink: 0;
    animation: pulse 2s infinite;
    background: linear-gradient(135deg, #667eea, #764ba2);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  @keyframes pulse {

    0%,
    100% {
      transform: scale(1);
    }

    50% {
      transform: scale(1.05);
    }
  }

  .balance-details {
    display: flex;
    flex-direction: row;
    gap: 0.5rem;
    flex: 1;
    align-items: center;
    justify-content: flex-start;
  }

  .balance-label {
    color: #333;
    font-size: 0.7rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.2px;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    white-space: nowrap;
  }

  .balance-amount {
    color: #667eea;
    font-size: 0.8rem;
    font-weight: 800;
    text-shadow: 0 1px 1px rgba(102, 126, 234, 0.3);
    font-family: 'Courier New', monospace;
    background: linear-gradient(135deg, #667eea, #764ba2);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    filter: drop-shadow(0 1px 1px rgba(0, 0, 0, 0.2));
  }

  /* Buttons */
  .btn {
    display: inline-flex;
    align-items: center;
    gap: 0.75rem;
    padding: 1rem 2rem;
    border: none;
    border-radius: 16px;
    font-size: 1.1rem;
    font-weight: 700;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    text-decoration: none;
    position: relative;
    overflow: hidden;
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
    direction: rtl;
    border: 1px solid rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
  }

  .btn::before {
    content: '';
    position: absolute;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
    transition: right 0.6s;
  }

  .btn:hover::before {
    right: 100%;
  }

  .btn-primary {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
  }

  .btn-primary:hover {
    background: linear-gradient(135deg, #5a6fd8 0%, #6a4b93 100%);
    transform: translateY(-4px);
    box-shadow: 0 12px 35px rgba(102, 126, 234, 0.5);
    border-color: rgba(255, 255, 255, 0.3);
  }

  .btn-secondary {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
  }

  .btn-secondary:hover {
    background: linear-gradient(135deg, #5a6fd8 0%, #6a4b93 100%);
    transform: translateY(-4px);
    box-shadow: 0 12px 35px rgba(102, 126, 234, 0.5);
    border-color: rgba(255, 255, 255, 0.3);
  }

  .btn-danger {
    background: linear-gradient(135deg, #dc3545 0%, #c82333 100%);
    color: white;
    box-shadow: 0 8px 25px rgba(220, 53, 69, 0.4);
  }

  .btn-danger:hover {
    background: linear-gradient(135deg, #c82333 0%, #a71e2a 100%);
    transform: translateY(-4px);
    box-shadow: 0 12px 35px rgba(220, 53, 69, 0.5);
    border-color: rgba(255, 255, 255, 0.3);
  }

  .btn-small {
    padding: 0.875rem 1.5rem;
    font-size: 1rem;
    border-radius: 12px;
  }

  /* Small Secondary Button - Same as regular secondary */
  .btn-small.btn-secondary {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
  }

  .btn-small.btn-secondary:hover {
    background: linear-gradient(135deg, #5a6fd8 0%, #6a4b93 100%);
    transform: translateY(-4px);
    box-shadow: 0 12px 35px rgba(102, 126, 234, 0.5);
    border-color: rgba(255, 255, 255, 0.3);
  }

  .btn-icon {
    width: 20px;
    height: 20px;
    transition: transform 0.3s ease;
    order: 2;
    filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.2));
  }

  .btn span {
    order: 1;
  }

  .btn:hover .btn-icon {
    transform: scale(1.1);
  }

  /* Button Disabled States */
  .btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
    transform: none !important;
    box-shadow: none !important;
  }

  .btn:disabled:hover {
    transform: none !important;
    box-shadow: none !important;
  }

  .btn:disabled::before {
    display: none;
  }

  /* Forms */
  .form-container {
    background: rgba(255, 255, 255, 0.98);
    backdrop-filter: blur(20px);
    border-radius: 20px;
    padding: 2.5rem;
    margin-bottom: 2.5rem;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.3);
    animation: slideIn 0.5s ease-out;
  }

  @keyframes slideIn {
    from {
      opacity: 0;
      transform: translateY(20px);
    }

    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  .form-container h3 {
    color: #333;
    margin-bottom: 1rem;
    font-size: 1.8rem;
    font-weight: 700;
    background: linear-gradient(135deg, #667eea, #764ba2);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  .form-container p {
    color: #666;
    margin-bottom: 2rem;
    font-size: 1.1rem;
    line-height: 1.6;
  }

  /* Form Groups */
  .form-group {
    margin-bottom: 1.5rem;
  }

  .form-group label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: 600;
    color: #2d3748;
    font-size: 0.95rem;
  }

  .form-group input,
  .form-group select {
    width: 100%;
    padding: 0.875rem 1rem;
    border: 2px solid #e2e8f0;
    border-radius: 12px;
    font-size: 1rem;
    transition: all 0.3s ease;
    background: white;
    color: #2d3748;
    font-family: inherit;
  }

  .form-group input:focus,
  .form-group select:focus {
    outline: none;
    border-color: #667eea;
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
    transform: translateY(-1px);
  }

  .form-group input:disabled,
  .form-group select:disabled {
    background-color: #f7fafc;
    color: #a0aec0;
    cursor: not-allowed;
    opacity: 0.7;
  }

  .form-group select {
    cursor: pointer;
    appearance: none;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%236b7280' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='m6 8 4 4 4-4'/%3e%3c/svg%3e");
    background-repeat: no-repeat;
    background-position: right 0.75rem center;
    background-size: 1.5em 1.5em;
    padding-right: 2.5rem;
    text-align: right;
    direction: rtl;
  }

  .form-group select option {
    padding: 0.5rem;
    background: white;
    color: #2d3748;
    text-align: right;
    direction: rtl;
  }

  .form-group select option:hover {
    background: #f7fafc;
  }

  .form-group select:focus {
    outline: none;
    border-color: #667eea;
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
    transform: translateY(-1px);
  }

  .form-group select:focus-visible {
    outline: none;
    border-color: #667eea;
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
    transform: translateY(-1px);
  }

  .form-group small {
    display: block;
    margin-top: 0.5rem;
    color: #718096;
    font-size: 0.875rem;
    line-height: 1.4;
  }

  .form-actions {
    display: flex;
    gap: 1rem;
    margin-top: 2rem;
    justify-content: flex-end;
  }

  /* Gates Grid */
  .gates-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
    gap: 2rem;
    margin-top: 2.5rem;
  }

  .gate-card {
    background: rgba(255, 255, 255, 0.98);
    backdrop-filter: blur(20px);
    border-radius: 20px;
    padding: 2rem;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    border: 1px solid rgba(255, 255, 255, 0.3);
    position: relative;
    overflow: hidden;
    direction: rtl;
  }

  .gate-card::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    height: 4px;
    background: linear-gradient(90deg, #667eea, #764ba2);
    transform: scaleX(0);
    transition: transform 0.3s ease;
  }

  .gate-card:hover::before {
    transform: scaleX(1);
  }

  .gate-card:hover {
    transform: translateY(-8px) scale(1.02);
    box-shadow: 0 30px 80px rgba(0, 0, 0, 0.15);
  }

  .gate-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.5rem;
    direction: rtl;
  }

  .gate-header h3 {
    color: #333;
    font-size: 1.5rem;
    font-weight: 700;
    background: linear-gradient(135deg, #667eea, #764ba2);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  .gate-actions-header {
    display: flex;
    gap: 0.75rem;
    direction: rtl;
  }

  .gate-info {
    margin-bottom: 1.5rem;
    padding: 1rem;
    background: rgba(102, 126, 234, 0.05);
    border-radius: 12px;
    border-right: 4px solid #667eea;
    direction: rtl;
  }

  .gate-info p {
    color: #666;
    margin-bottom: 0.75rem;
    font-size: 0.95rem;
    line-height: 1.5;
  }

  .gate-info strong {
    color: #333;
    font-weight: 600;
  }

  .gate-authorized {
    margin-bottom: 2rem;
    padding: 1rem;
    background: rgba(40, 167, 69, 0.05);
    border-radius: 12px;
    border-right: 4px solid #28a745;
    direction: rtl;
  }

  .gate-authorized h4 {
    color: #333;
    font-size: 1.1rem;
    margin-bottom: 0.75rem;
    display: flex;
    align-items: center;
    gap: 0.75rem;
    font-weight: 600;
  }

  .icon-small {
    width: 18px;
    height: 18px;
    color: #28a745;
    display: inline-block;
    vertical-align: middle;
    margin-left: 0.5rem;
    flex-shrink: 0;
  }

  .icon-big {
    width: 64px;
    height: 64px;
    color: #28a745;
    display: inline-block;
    vertical-align: middle;
    margin-left: 0.5rem;
  }

  .authorized-number {
    color: #28a745;
    font-weight: 700;
    font-size: 1rem;
    font-family: 'Courier New', monospace;
    background: rgba(40, 167, 69, 0.1);
    padding: 0.5rem 1rem;
    border-radius: 8px;
    border: 1px solid rgba(40, 167, 69, 0.3);
    display: inline-block;
  }

  .authorized-number.hidden {
    color: #6c757d;
    font-family: monospace;
    letter-spacing: 3px;
    user-select: none;
    background: rgba(108, 117, 125, 0.1);
    border: 1px solid rgba(108, 117, 125, 0.3);
  }

  .password-notice {
    color: #6c757d;
    font-style: italic;
    font-size: 0.85rem;
    margin-top: 0.75rem;
    text-align: center;
    padding: 0.5rem;
    background: rgba(108, 117, 125, 0.1);
    border-radius: 8px;
    border: 1px solid rgba(108, 117, 125, 0.2);
  }

  .gate-actions {
    display: flex;
    gap: 1rem;
  }

  /* Gate Open Button Special Styling */
  .gate-open-btn {
    flex: 1;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
    border: 1px solid rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
    position: relative;
    overflow: hidden;
  }

  .gate-open-btn::before {
    content: '';
    position: absolute;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
    transition: right 0.6s;
  }

  .gate-open-btn:hover::before {
    right: 100%;
  }

  .gate-open-btn:hover {
    background: linear-gradient(135deg, #5a6fd8 0%, #6a4b93 100%);
    transform: translateY(-4px);
    box-shadow: 0 12px 35px rgba(102, 126, 234, 0.5);
    border-color: rgba(255, 255, 255, 0.3);
  }

  .gate-open-btn:active {
    transform: translateY(-2px);
    box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
  }

  /* Success Message */
  .success-message {
    background: linear-gradient(135deg, #d4edda, #c3e6cb);
    color: #155724;
    padding: 1.5rem;
    border-radius: 12px;
    margin-bottom: 1.5rem;
    border: 1px solid #c3e6cb;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 8px 25px rgba(40, 167, 69, 0.2);
    animation: slideIn 0.5s ease-out;
    direction: rtl;
    text-align: right;
  }

  .success-message button {
    background: none;
    border: none;
    color: #155724;
    font-size: 1.5rem;
    cursor: pointer;
    padding: 0;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: all 0.3s ease;
    margin-right: auto;
    margin-left: 0;
  }

  .success-message button:hover {
    background: rgba(40, 167, 69, 0.2);
    transform: scale(1.1);
  }

  /* Error Message */
  .error-message {
    background: linear-gradient(135deg, #f8d7da, #f5c6cb);
    color: #721c24;
    padding: 1.5rem;
    border-radius: 12px;
    margin-bottom: 1.5rem;
    border: 1px solid #f5c6cb;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 8px 25px rgba(220, 53, 69, 0.2);
    animation: shake 0.5s ease-in-out;
    direction: rtl;
    text-align: right;
  }

  @keyframes shake {

    0%,
    100% {
      transform: translateX(0);
    }

    25% {
      transform: translateX(5px);
    }

    75% {
      transform: translateX(-5px);
    }
  }

  .error-message button {
    background: linear-gradient(135deg, #dc3545, #c82333);
    color: white;
    border: none;
    padding: 0.75rem 1.5rem;
    border-radius: 8px;
    cursor: pointer;
    font-weight: 600;
    transition: all 0.3s ease;
    margin-right: auto;
    margin-left: 0;
  }

  .error-message button:hover {
    background: linear-gradient(135deg, #c82333, #a71e2a);
    transform: translateY(-2px);
  }

  /* Enhanced Loading */
  .loading {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 400px;
    color: white;
    font-size: 1.4rem;
    font-weight: 600;
    direction: rtl;
    text-align: center;
  }

  .loading-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
  }

  .loading-spinner {
    width: 60px;
    height: 60px;
    border: 4px solid rgba(255, 255, 255, 0.3);
    border-top: 4px solid white;
    border-radius: 50%;
    animation: spin 1s linear infinite;
  }

  .loading p {
    margin: 0;
    font-size: 1.2rem;
    opacity: 0.9;
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  /* Enhanced No Gates State */
  .no-gates {
    text-align: center;
    color: rgba(255, 255, 255, 0.9);
    padding: 4rem 2rem;
    font-size: 1.3rem;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    border-radius: 20px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    margin-top: 2rem;
    direction: rtl;
  }

  .no-gates-icon {
    font-size: 4rem;
    margin-bottom: 1rem;
    filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.2));
  }

  .no-gates h3 {
    color: white;
    font-size: 1.8rem;
    margin-bottom: 1rem;
    font-weight: 600;
  }

  .no-gates p {
    color: rgba(255, 255, 255, 0.8);
    margin-bottom: 2rem;
    font-size: 1.1rem;
    line-height: 1.6;
  }

  /* Form Enhancements */
  .form-group input:disabled+small {
    opacity: 0.7;
  }

  /* Enhanced Card Hover Effects */
  .gate-card:hover .gate-header h3 {
    background: linear-gradient(135deg, #667eea, #764ba2);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  /* RTL Text Selection */
  ::selection {
    background: rgba(102, 126, 234, 0.3);
    color: #333;
  }

  ::-moz-selection {
    background: rgba(102, 126, 234, 0.3);
    color: #333;
  }

  /* RTL Scrollbar */
  ::-webkit-scrollbar {
    width: 8px;
  }

  ::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 4px;
  }

  ::-webkit-scrollbar-thumb {
    background: rgba(102, 126, 234, 0.5);
    border-radius: 4px;
  }

  ::-webkit-scrollbar-thumb:hover {
    background: rgba(102, 126, 234, 0.7);
  }

  /* RTL Responsive Adjustments */
  @media (max-width: 768px) {
    .dashboard-header {
      flex-direction: column;
      gap: 2rem;
      padding: 2rem;
      text-align: center;
    }

    .dashboard-header>div:first-child {
      order: 2;
    }

    .dashboard-header>button {
      order: 1;
      align-self: stretch;
      width: 100%;
      max-width: 300px;
      margin: 0 auto;
    }

    .dashboard-header h1 {
      font-size: 2.5rem;
      text-align: center;
    }

    .dashboard-header p {
      font-size: 1.1rem;
      text-align: center;
    }

    .admin-notice,
    .password-status {
      justify-content: center;
      text-align: center;
    }

    .form-actions {
      flex-direction: column;
      align-items: stretch;
    }

    .btn {
      justify-content: center;
    }
  }

  @media (max-width: 480px) {
    .dashboard-header {
      padding: 1.5rem;
      gap: 1.5rem;
    }

    .dashboard-header h1 {
      font-size: 2rem;
    }

    .dashboard-header p {
      font-size: 1rem;
    }

    .dashboard-header>button {
      padding: 0.875rem 1.5rem;
      font-size: 1rem;
    }

    .admin-notice,
    .password-status {
      font-size: 1rem;
      padding: 0.625rem 1rem;
    }
  }

  /* Responsive Enhancements for other components */
  @media (max-width: 768px) {
    .dashboard-container {
      padding: 1rem;
    }

    .gates-grid {
      grid-template-columns: 1fr;
      gap: 1.5rem;
    }

    .form-container {
      padding: 1.5rem;
    }

    .gate-card {
      padding: 1.5rem;
    }

    .success-message {
      padding: 1rem;
      flex-direction: column;
      gap: 1rem;
      text-align: center;
    }

    .success-message button {
      align-self: center;
    }

    .loading-spinner {
      width: 50px;
      height: 50px;
    }

    .no-gates {
      padding: 3rem 1.5rem;
    }

    .no-gates-icon {
      font-size: 3rem;
    }

    .no-gates h3 {
      font-size: 1.5rem;
    }
  }

  @media (max-width: 480px) {
    .success-message {
      font-size: 0.9rem;
    }

    .loading {
      font-size: 1.2rem;
    }

    .no-gates {
      padding: 2rem 1rem;
    }

    .balance-amount {
      font-size: 1.5rem;
    }
  }

  .verified-callers-info .btn-small .btn-icon {
    width: 16px;
    height: 16px;
    margin: 0;
    flex-shrink: 0;
  }

  .form-group select:hover {
    border-color: #cbd5e0;
    background-color: #fafbfc;
  }

  .form-group select:hover:not(:disabled) {
    border-color: #cbd5e0;
    background-color: #fafbfc;
  }
}